import * as THREE from 'three'
// import { DeviceOrientationControls } from './SensorControl'

class BlankScene {
    constructor() {
        this.camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.01, 10);

        this.camera.position.z = 0;
        // this.controls = new DeviceOrientationControls(this.camera)

        this.scene = new THREE.Scene();

        // this.geometry = new THREE.BoxGeometry(0.2, 0.2, 0.2);
        this.geometry = new THREE.SphereGeometry(5, 8, 3);
        // invert the geometry on the x-axis so that all of the faces point inward
        this.geometry.scale(- 1, 1, 1);

        this.material = new THREE.MeshBasicMaterial({ color: 0xff00ff, wireframe: true });
        this.mesh = new THREE.Mesh(this.geometry, this.material);
        this.scene.add(this.mesh);



        this.renderer = new THREE.WebGLRenderer({ antialias: true });
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.animation = () => {

            // this.mesh.rotation.x = time / 20000;
            // this.mesh.rotation.y = time / 1000;
            // this.camera.position.z -= 0.01;
            // this.camera.rotation.y += 0.003
            // this.camera.rotation.x += 0.001
            // if (this.camera.position.z > 10) console.log('far')
            // console.log(time, this.camera.position.z)

            window.requestAnimationFrame(this.animation);
            // this.controls.update();

            this.renderer.render(this.scene, this.camera);

        }
        this.renderer.setAnimationLoop(this.animation);


        this.loadImage = (i, x, y, z) => {
            console.log('loading texture...')
            console.log(i)
            new THREE.ImageLoader()
                .setCrossOrigin('*')
                .load(i, (image) => {

                    const texture = new THREE.CanvasTexture(image);
                    const material = new THREE.MeshBasicMaterial({ color: 0xff8888, map: texture });
                    // console.log(material)
                    const geometry = new THREE.PlaneGeometry(5, 6);
                    const cube = new THREE.Mesh(geometry, material);
                    cube.position.set(x, y, z)
                    this.scene.add(cube)
                    // addCube(material);

                }, function () {
                    // console.log(p)
                }, function (err) {
                    console.log(err)
                });

            // new THREE.TextureLoader().load()
        }

        document.body.appendChild(this.renderer.domElement);

        // setTimeout(() => { this.loadImage(require('./assets/textures/scene_d.jpg'), 2.5, 0, -4.9) }, 0)
        // setTimeout(() => { this.loadImage(require('./assets/textures/scene_f.jpg'), -2.5, 0, -4.9) }, 1000)
        // setTimeout(() => { this.loadImage(require('./assets/textures/scene_u.jpg'), -7.5, 0, -4.9) }, 2000)
        // setTimeout(() => { this.loadImage(require('./assets/textures/scene_f.jpg'), 7.5, 0, -4.9) }, 3000)

        this.localLoad = () => {
            this.loadImage(require('./assets/textures/scene_d.jpg'), 2.5, 0, -4.9)
        }
        
        document.addEventListener('myevent', this.localLoad, false)
    }

}


export default BlankScene